import mongoose from "mongoose";

const RoomSchems = mongoose.Schema(
  {
    checkIn: {
      type: Date,
    },
    checkOut: {
      type: Date,
    },
    status: {
      type: String,
      required: true,
      default: "available",
    },
  },
  { timestamps: true }
);

export default mongoose.model("room", RoomSchems);
